<template>
    <div class="main">
<div class="line"></div>
<el-menu
  :default-active="activeIndex2"
  class="el-menu-demo"
  mode="horizontal"
  @select="handleSelect"
  background-color="#545c64"
  text-color="#fff"
  active-text-color="#ffd04b">
  <el-menu-item index="1" @click="main">公司信息</el-menu-item>
  <el-menu-item index="2"  @click="workerInforma">员工管理</el-menu-item>
  <el-menu-item index="4" @click="moneyList">工资计算</a></el-menu-item>
  <el-submenu index="3">
    <template slot="title">用户操作</template>
    <el-menu-item index="2-1" @click="changePass">修改密码</el-menu-item>
    <el-menu-item index="2-2" @click="loginOut">退出登录</el-menu-item>
  </el-submenu>
</el-menu>
   <div id="view">
       <router-view></router-view>
   </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
        activeIndex2: '1',
        hahaha:1,
        dialog:false,
        show:true
        }
    },methods: {
    handleSelect(key, keyPath) {
       // console.log(key, keyPath);
      }, 
    
    main:function(){
             this.$router.push('main');
    },
    workerInforma:function(){
             this.$router.push('workerInform');
    },
  
    moneyList:function(){
               this.$router.push('moneyList'); 
    },

    loginOut:function(){
                this.$router.push("/")
               this.$router.go(0)

    },
    changePass:function(){
        this.$router.push('changePassword')
    }
    }
}
</script>
<style scoped>

#view{
    height: 900px;
    padding-top: 61px
} 
.el-menu-demo{
     position:fixed;
     top: 0;
     z-index:999;
     width: 100%;
}
</style>


