<template>
    <div class="record">
    <p class="nameBox"> <el-input v-model="nameInput" placeholder="请输入员工姓名" class="input" ></el-input><el-button :plain="true" @click="nameFound">查找</el-button>
           <el-input v-model="numberInput" placeholder="请输入职工号" class="input"></el-input><el-button :plain="true" @click="numberFound">查找</el-button> </p>
        
    <el-table
    :data="workerDatas"  style="width: 100%">
    <el-table-column     prop="Name"    label="姓名"    width="120">
    </el-table-column>
    <el-table-column   prop="Number"   label="职工号"   width="120">
    </el-table-column>
    <el-table-column    fixed="right"    label="操作"  width="800">
      <template slot-scope="scope">
        <el-button @click="dialog=true;handleClick(scope.row)" type="text" size="small">添加考勤信息</el-button>
                    <el-dialog title="添加信息" :visible.sync="dialog" :modal="false">
                            <el-form :model="form">
                                <el-form-item label="员工姓名:" :label-width="formLabelWidth">
                                    <p>{{form.Name}}</p>   
                                </el-form-item>
                                <el-form-item label="员工编号:" :label-width="formLabelWidth">
                                <p>{{form.Number}}</p>
                                </el-form-item>
                                <el-form-item label="选择考勤类型" :label-width="formLabelWidth">
                                <el-select v-model="form.some" placeholder="选择考勤类型">
                                    <el-option label="迟到" value="迟到"></el-option>
                                    <el-option label="请假" value="请假"></el-option>
                                    <el-option label="加班" value="请假"></el-option>
                                </el-select>
                                </el-form-item>
                                <el-form-item label="时间:" :label-width="formLabelWidth">
                                         <el-input v-model="timeYear" class="inputYear" autocomplete="off" type="number" min="2000" max="2030"></el-input>年
                                         <el-input v-model="timeMouth" class="inputtime" autocomplete="off" type="number" min="1" max="12"></el-input>月
                                         <el-input v-model="timeDay" class="inputtime" autocomplete="off" type="number" min="1" max="31"></el-input>日
                                </el-form-item> 
                               
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialog = false">取 消</el-button>
                                <el-button type="primary" @click="changeSure">确 定</el-button>
                            </div>
                            </el-dialog>
      </template>
    </el-table-column>
  </el-table>
 

    </div>
</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
export default {
    data(){
        return{
        dialog: false,
        workerDatas:[],
        timeYear:'',
        timeMouth:'',
        id:0,
        timeDay:'',
        formLabelWidth: '120px',
        nameInput:'',
        numberInput:'',
        form: {
                    Name: '',
                    Number:'',
                    some:'',
              },
        }
    }, created:function(){                                     //created 虚拟dom创建时就开始执行的函数  
                 axios.get(this.HOST+'/worker/getRecordList')            //从后台拿到json数组
                .then(reponse=>{
                    let getRecordList=reponse.data.data            //只取用json数组里的data数据项并把该项数据赋值给workerDatas数组
                    let List = getRecordList
                    let number = []             
                    for(let i=0;i<List.length;i++){
                       number[i]=List[i].id                         //把data里的数据取出Number项列成一个数组
                   }
                   let newNumber =  parseInt(number[number.length-1])
                   this.id=newNumber+1  
                    console.log(this.id)
                   
               })
                let time = new Date();
                 let timeStr =String(time)
                let timeCut = timeStr.split(' ')
                //console.log(timeCut)
                 this.timeYear = parseInt(timeCut[3])
                 this.timeDay = parseInt(timeCut[2])
                 let timeMouth = timeCut[1]

                 if(timeMouth=='January'){
                     this.timeMouth= 1
                 }else if(timeMouth=='February'){
                     this.timeMouth=2
                 }else if(timeMouth=='March'){
                     this.timeMouth=3
                 }else if(timeMouth=='April'){
                     this.timeMouth=4
                 }else if(timeMouth=='May'){
                     this.timeMouth=5
                 }else if(timeMouth=='June'){
                     this.timeMouth=6
                 }else if(timeMouth=='July'){
                     this.timeMouth=7
                 }else if(timeMouth=='August'){
                     this.timeMouth=8
                 }else if(timeMouth=='September'){
                     this.timeMouth=9
                 }else if(timeMouth=='October'){
                     this.timeMouth=10
                 }else if(timeMouth=='November'){
                     this.timeMouth=11
                 }else if(timeMouth=='December'){
                     this.timeMouth=12
                 }
               
            },


    methods:{

         handleSizeChange(val) {
                    this.currentPage = 1;
                    this.pageSize = val;
                    },
            handleCurrentChange(val) {
                        this.currentPage = val;
                    },   
         handleClick(row) {
       // console.log(row);
      },


        nameFound:function(){
                   let nameInputResult=this.nameInput;            //拿到输入内容
                   let reg=/^[\u4e00-\u9fa5]+$/;              //汉字输入的正则表达式
                    if(!reg.test(nameInputResult)){
                          this.$message({
                            showClose: true,
                            message: '输入有误，请输入正确的中文名字',
                            type: 'error'
                        });
                           
                       }else{

                            axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workrName':this.nameInput,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundName',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                   this.nameInput=''
                                    //console.log(res)
                                    if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '未查找到该员工的信息',
                                          type: 'error'
                                          });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                         }
                },


                //按员工号查找
                numberFound:function(){
                let numberInputResult = parseInt(this.numberInput);                    //获取输入值              
                   let rem=/^\d{7}$/;                                         //数字输入的正则表达式
                    if(!rem.test(numberInputResult)){
                      
                           this.$message({
                            showClose: true,
                            message: '未查找到该用户，请确认职工号是否输入正确',
                            type: 'error'
                        });
                        //alert(
                          // alert("未查找到该用户，请确认职工号是否输入正确")
                       }else{
                            

                           axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workerNumber':numberInputResult,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundNumber',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                   this.numberInput=''
                                   if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '未查找到该员工的信息',
                                        type: 'error'
                                       });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                    }

                    },
                    //查询出每一行中的内容，把内容显示到这一行修改界面之中
                handleClick(id) {
                    let foundNumber = id.Number            //拿到传入的参数 
                    let number=this.workerDatas                     
                    let numbers = []       
                    for(let i=0;i<number.length;i++){
                        numbers[i]=number[i].Number              //从所有数据中拿到Number数据
                    }
                    let result = numbers.indexOf(foundNumber)        //匹配传入的参数在整个数据中的位置
                    if(result!=-1){
                        this.form = this.workerDatas[result]          //把这一项内容赋值给form数据，然后修改弹出页面匹配
                    }     
                    },

                changeSure:function(){
                   //console.log('1')
                   if(this.form.some==null){
                       this.$message({
                                        showClose: true,
                                        message: '记录失败，请选择一项考勤信息',
                                        type: 'error'
                                        });
                   }else{
                                 
                        axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'Name': this.form.Name,
                                    'Number':this.form.Number,
                                    'some':this.form.some,
                                    'id':this.id,
                                    'timeYear':this.timeYear,
                                     'timeMouth':this.timeMouth,
                                     "timeDay":this.timeDay
                                });       
                                axios({
                                        url:this.HOST+'/worker/addRecord',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                  .then((res)=>{
                                     
                                     // console.log(res)
                                     if(res.data.message=="数据已经存在"){
                                        this.$message({
                                        showClose: true,
                                        message: '该记录已经存在',
                                        type: 'error'
                                        });
                                        this.dialog = false
            
                                    }else if (res.data.message=="添加成功") {
                                        this.$message({
                                        showClose: true,
                                        message: '添加记录成功',
                                        type: 'success'
                                        });
                                      this.dialog = false
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '插入记录失败',
                                        type: 'error'
                                        });
                                    }
                                    //console.log(res);
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                     
                            }
                 }

          },
   
}
</script>
<style scoped>
 .input{
    width: 200px;
}   
 .inputtime{
    width: 70px;
}   
 .inputYear{
    width: 80px;
}  
.nameBox{
    text-align: center;
} 
el-dialog{
    text-align: center;
}
</style>


