<template>
    <div class="inform">
        <br><br>
             <p class="nameBox"> <el-input v-model="nameInput" placeholder="请输入员工姓名" class="input" ></el-input><el-button :plain="true" @click="nameFound">查找</el-button>
           <el-input v-model="numberInput" placeholder="请输入职工号" class="input"></el-input><el-button :plain="true" @click="numberFound">查找</el-button> 
            <el-button type="" @click="all">更新员工列表</el-button>
            <el-button type="text" @click="dialog = true">添加新员工</el-button>

                 <el-dialog title="添加新员工" :visible.sync="dialog" >
                                    <el-form :model="form">
                                <el-form-item label="员工编号:" :label-width="formLabelWidth">
                                     <el-input v-model="inserta.Name" class="input" autocomplete="off"></el-input>
                                </el-form-item>
                                 <el-form-item label="员工姓名:" :label-width="formLabelWidth">
                                <el-input v-model="inserta.Name" class="input" autocomplete="off"></el-input>
                                </el-form-item>

                                 <el-form-item label="员工电话:" :label-width="formLabelWidth">
                                <el-input v-model="inserta.Phone" class="input" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="员工身份证号:" :label-width="formLabelWidth">
                                <el-input v-model="inserta.Address" class="input" autocomplete="off"></el-input>
                                </el-form-item>

                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button :plain="true" type="primary" @click="addSure">确 定</el-button>
                                <el-button @click="dialog = false">取 消</el-button>
                                
                            </div>
                            </el-dialog>

           </p>
            <br>
            <br>
   
            <el-table :data="workerDatas.slice((currentPage-1)*pageSize,currentPage*pageSize)" border :row-class-name="tableRowClassName" class="tableMain" style="width: 80%"  >
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label="姓名:">
                        <span>{{ props.row.xm }}</span>
                    </el-form-item>
                    <el-form-item label="性别:">
                        <span>{{ props.row.xb }}</span>
                    </el-form-item>
                     <el-form-item label="员工编号:">
                        <span>{{ props.row.ygid }}</span>
                    </el-form-item>
                    <el-form-item label="出生地址:">
                        <span>{{ props.row.csdz }}</span>
                    </el-form-item>
                    <el-form-item label="身份证号:">
                        <span>{{ props.row.sfzh }}</span>
                    </el-form-item>
                    <el-form-item label="国籍:">
                        <span>{{ props.row.gj }}</span>
                    </el-form-item>
                    <el-form-item label="职业:">
                        <span>{{ props.row.zy }}</span>
                    </el-form-item>
                    <el-form-item label="文化程度:">
                        <span>{{ props.row.whcd }}</span>
                    </el-form-item>
                    <el-form-item label="联系地址:">
                        <span>{{ props.row.lxdz }}</span>
                    </el-form-item>
                    <el-form-item label="婚姻状况:  ">
                        <span>{{ props.row.hyzk }}</span>
                    </el-form-item>
                    <el-form-item label="联系电话:">
                        <span>{{ props.row.lxdh }}</span>
                    </el-form-item>
                    </el-form>
                </template>
                </el-table-column>
            
            <el-table-column prop="xm" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="ygid" label="员工编号" width="180">
            </el-table-column>
            <el-table-column prop="bz" label="备注" width="180">
            </el-table-column>
             <el-table-column prop="caozuo" label="操作">
                   <template slot-scope="scope">
                        <el-button type="text" @click="dialogFormVisible = true;handleClick(scope.row)" class="fz">修改</el-button>
                            <el-dialog title="修改信息" :visible.sync="dialogFormVisible" >
                                    <el-form :model="form">
                                <el-form-item label="姓名:" :label-width="formLabelWidth">
                                    <el-input v-model="form.xm  " class="input" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="员工编号:" :label-width="formLabelWidth">
                                       <el-input v-model="form.ygid" class="input" autocomplete="off"></el-input>
                                </el-form-item>
                              <el-form-item label="员工电话:" :label-width="formLabelWidth">
                                <el-input v-model="form.lxdh" class="input" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="员工身份证号:" :label-width="formLabelWidth">
                                <el-input v-model="form.sfzh" class="input" autocomplete="off"></el-input>
                                </el-form-item>

                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button :plain="true" type="primary" @click="changeSure">确 定</el-button>
                                <el-button @click="dialogFormVisible = false">取 消</el-button>
                                
                            </div>
                            </el-dialog>

                 <el-button @click.native.prevent="deleteList(scope.row,scope.$index,workerDatas)"type="text" >删除</el-button>
                  </template>
            </el-table-column>
        </el-table>
            <div class="block" style="margin-top:15px;">
             <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[2,5,15,20]"  layout="total, sizes, prev, pager, next, jumper" :total="workerDatas.length">
               </el-pagination>
           </div>  

    
    </div> 
</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
    export default {
            name:'inform',
            data(){
                return{
                workerDatas:[],
                dialogFormVisible: false,
                dialog:false,
                formLabelWidth: '120px',
                nameInput:'',
                numberInput:'',
                currentPage: 1, // 当前页码
                total: 20, // 总条数
                pageSize: 2, // 每页的数据条数
                look:'11',              
                inserta:{
                     Number:'',
                     Name:'',
                     Phone:'',
                     Address:'',
                     Job:''   
                },
                form: {
                    xm: '',
                    ygid:'',
                    lxdh:'',
                    sfzh:'',
              },
                   
                    
                }
            },                                                            
            created:function(){   
                      axios.defaults.withCredentials = true          
                        for(let i=1;i<=100;i++){           
                                let da1 =  qs.stringify({
                                    'ygid':i,
                                });       
                                axios({
                                        url:"http://182.151.22.247:8800/Family/findByygid",
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                   if(res.data.data.length!=0){
                                      // console.log(res.data.data[0].gj)
                                       if(res.data.data[0].xb=="1"){
                                           res.data.data[0].xb = '男'
                                       }else if(res.data.data[0].xb=="2"){
                                            res.data.data[0].xb = '女'
                                       }
                                       if(res.data.data[0].gj=="CN"){
                                           res.data.data[0].gj = "中国"
                                       }
                                       this.workerDatas.push(res.data.data[0]);
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });





                    }
                       console.log(this.workerDatas) 



                // }
            //    axios.get("http://182.151.22.247:8800/Cretifi/findPageById")
            //    .then(reponse=>{
            //        console.log(reponse)
            //    })
            },
            methods:{
            handleSizeChange(val) {
                    this.currentPage = 1;
                    this.pageSize = val;
                    },
            handleCurrentChange(val) {
                        this.currentPage = val;
                    },                                                                  //利用element-ui的ui组建实现table列表分页显示
            changeWoker:function(id){


            },

            tableRowClassName({row, rowIndex}){
                // console.log(row)
                // console.log(rowIndex)
                    if (rowIndex === 1) {
                        return 'warning-row';
                    } else if (rowIndex === 3) {
                        return 'success-row';
                 }
                        return '';
            },
             nameFound:function(){
                   let nameInputResult=this.nameInput;            //拿到输入内容
                   let reg=/^[\u4e00-\u9fa5]+$/;              //汉字输入的正则表达式
                    if(!reg.test(nameInputResult)){
                          this.$message({
                            showClose: true,
                            message: '输入有误，请输入正确的中文名字',
                            type: 'error'
                        });
                           
                       }else{

                            axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workrName':this.nameInput,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundName',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    this.nameInput=''
                                    //console.log(res)
                                    if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '未查找到该员工的信息',
                                        type: 'error'
                                       });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                         }
                },


                //按员工号查找
                numberFound:function(){
                let numberInputResult = parseInt(this.numberInput);                    //获取输入值              
                //    let rem=/^\d{7}$/;                                         //数字输入的正则表达式
                //     if(!rem.test(numberInputResult)){
                      
                //            this.$message({
                //             showClose: true,
                //             message: '输入有误，请输入7位职工号',
                //             type: 'error'
                //         });
                //        }else{
                          //  console.log(this.workerDatas)

                           axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'ygid':numberInputResult,
                              
                                });       
                                axios({
                                        url:"http://182.151.22.247:8800/Family/findByygid",
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                   console.log(res)
                                   this.numberInput=''
                                   if(res.data.code==200){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '未查找到该员工的信息',
                                        type: 'error'
                                       });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                    // }

                    },

            //查询出每一行中的内容，把内容显示到这一行修改界面之中
                handleClick(id) {
                    // console.log(id)
                        this.form = id
                    },

            //修改信息
              changeSure:function(){
                    let inputName = this.form.Name
                    let inputPhone=this.form.Phone
                    let inputAdrress=this.form.Address
                   // console.log(inputName)
                    let regName=/^[\u4e00-\u9fa5]+$/
                    let regPhone=/^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/
                    if(!regName.test(inputName)){
                        this.$message({
                            showClose: true,
                            message: '姓名格式不正确',
                            type: 'error'
                        });
                        alert('姓名格式不正确')
                    }else if(!regPhone.test(inputPhone)){
                        this.$message({
                            showClose: true,
                            message: '电话号码格式不正确',
                            type: 'error'
                        });
                        //alert('电话号码格式不正确')
                    }else{
                          axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'inputName': this.form.Name,
                                    'inputPhone':this.form.Phone,
                                    'inputAddress':this.form.Address,
                                     'workrNumber':this.form.Number,
                                     "Job":this.form.Job
                                });       
                                axios({
                                        url:this.HOST+'/worker/changeWorker',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                  .then((res)=>{
                                     if(res.code=1){
                                        this.$message({
                                        showClose: true,
                                        message: '修改信息成功',
                                        type: 'success'
                                        });
                                        this.dialogFormVisible = false
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '修改信息失败',
                                        type: 'error'
                                        });
                                    }
                                    //console.log(res);
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                    }

              },

            all:function(){
                axios.get(this.HOST+'/worker/getList')            //从后台拿到json数组
                .then(reponse=>{
                    this.workerDatas=reponse.data.data            //只取用json数组里的data数据项并把该项数据赋值给workerDatas数组
                })
            },

               //删除信息  
   

            deleteList:function(id,index,rows){
               // console.log(id.Number)
                 this.$confirm('此操作将永久删除该员工, 是否继续?', '提示', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                            }).then(() => {
                             axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'found': id.Number,
                                });       
                                axios({
                                        url:this.HOST+'/worker/deleteList',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then(function(res) {
                                    rows.splice(index, 1);
                                    //console.log(res);
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                       }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: '已取消删除'
                            });          
                            });    
                 },
                 //添加新员工
                 addSure:function(){
                        let regName=/^[\u4e00-\u9fa5]+$/
                    let regPhone=/^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/
                    let inputName = this.inserta.Name
                    let inputPhone=this.inserta.Phone
                    let inputAdrress=this.inserta.Address
                    if(!regName.test(inputName)){
                        this.$message({
                            showClose: true,
                            message: '姓名格式不正确',
                            type: 'error'
                        });
                       // alert("姓名格式不正确")
                    }else if(!regPhone.test(inputPhone)){
                        this.$message({
                            showClose: true,
                            message: '电话号码格式不正确',
                            type: 'error'
                        });
                       // alert('电话号码格式不正确')
                    }else if(this.inserta.Job==''){
                
                                        this.$message({
                                        showClose: true,
                                        message: '请选择一项工作',
                                        type: 'error'
                        });

                    }else{
                                    axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workrNumber':this.inserta.Number,
                                    'inputName': this.inserta.Name,
                                    "inputJob":this.inserta.Job,
                                    'inputPhone':this.inserta.Phone,
                                    'inputAddress':this.inserta.Address
                                });       
                                axios({
                                        url:this.HOST+'/worker/add',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                     if(res.code=1){
                                        this.$message({
                                        showClose: true,
                                        message: '录入信息成功',
                                        type: 'success'
                                        });
                                    this.inserta.Number+=1
                                    this.dialog = false
                                    // let List = res.data.data
                                    // let number = []             
                                    // for(let i=0;i<List.length;i++){
                                    //     number[i]=List[i].Number                         //把data里的数据取出Number项列成一个数组
                                    // }
                                    // let newNumber =  parseInt(number[number.length-1])
                                    // this.inserta.Number=newNumber+1
            
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '录入信息失败',
                                        type: 'error'
                                        });
                                    }
                                    //console.log(res);
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                                
                                

                        }
                 }
     },
         
           
            
}
</script>
<style scoped>

.inform{
    background-color: white
}

 .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
.input{
    width: 200px;
}
.tableMain{
    margin-left: 5%
}
.nameBox{
 text-align: center;
}
el-dialog{
    text-align: center;
}
tableMain{
    height: 100%;
}
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>


