<template>
    <div id="changePassword">
        <div class="li">
        <p>修改密码</p><br>
        <el-form  status-icon label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户名：" prop="userName">
                <el-input type="userName"  v-model="userName" class="input" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="原密码：" prop="pass">
                <el-input type="password"  v-model="oldPassword" class="input" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="新密码：" prop="checkPass">
                <el-input type="password" v-model="newPassword" class="input" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="确认新密码：" prop="age">
                <el-input type="password" v-model="determinePassword"  class="input"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="determine">提交</el-button>
            </el-form-item>
             <el-form-item label="" prop="pass">
               <p class="notice">温馨提示：</p><p>新密码格式需要至少1位小写字母，1位大写字母，1位数字的8到16位密码</p>
            </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import qs from 'qs';
export default {
    data(){
        return{
            userName:'',
           oldPassword:'',
           newPassword:'',
           determinePassword:''
        }
    },
        methods:{
            determine:function(){
                let newPassword=this.newPassword
                let determinePassword=this.determinePassword
                let pattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/
                // if(!pattern.test(newPassword)){
                //      this.$message({
                //             showClose: true,
                //             message: '新密码格式不正确',
                //             type: 'error'
                //         });
                // }else 
                if(newPassword!=determinePassword){
                        this.$message({
                            showClose: true,
                            message: '两次新密码输入不一样',
                            type: 'error'
                        });
                }else{
                                 axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'userName':this.userName,
                                    'oldPassword':this.oldPassword,
                                    'newPassword':this.newPassword,
                                });       
                                axios({
                                        url:this.HOST+'/worker/changePass',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    console.log(res)
                                    if(res.data.code==1){
                                        this.$message({
                                        showClose: true,
                                        message: '密码修改成功',
                                        type: 'success'
                                      });
                                        this.$router.push('/')
                                        this.$router.go(0)

                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '修改失败，原密码错误',
                                        type: 'error'
                                         });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                }
            }
        }
}
</script>
<style scoped>
.li{
    text-align: center;
     height:985px;
        background-color: #e0e8f5;
}
.demo-ruleForm{
    width: 500px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
}
.notice{
    color:red   
}

</style>


