<template>
        <div>
         <el-table
    :data="workerDatas.slice((currentPage-1)*pageSize,currentPage*pageSize)"  style="width: 100%">
    <el-table-column     prop="Name"    label="姓名"    width="120">
    </el-table-column>
    <el-table-column   prop="Number"   label="职工号"   width="120">
    </el-table-column>
    <el-table-column   prop="Record"   label="考勤情况"   width="120">
    </el-table-column>
    <el-table-column   prop="time"   label="考勤时间"   width="120">
    </el-table-column>
    <el-table-column    fixed="right"    label="操作"  width="650">
    <template slot-scope="scope">
     <el-button @click.native.prevent="deleteList(scope.row,scope.$index,workerDatas)"type="text" size="small">删除</el-button>
     </template>
    </el-table-column>
  </el-table>
 <div class="block" style="margin-top:15px;">
             <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10]"  layout="total, sizes, prev, pager, next, jumper" :total="workerDatas.length">
               </el-pagination>
           </div>  
   
    </div>
</template>

<script>

import axios from 'axios';
import qs from 'qs';


export default {
    data(){
        return{
        workerDatas:[],
        currentPage: 1, // 当前页码
        total: 20, // 总条数
        pageSize: 10, // 每页的数据条数
        formLabelWidth: '120px',
        }
    }, created:function(){                                     //created 虚拟dom创建时就开始执行的函数  
                axios.get(this.HOST+'/worker/getRleaxList')            //从后台拿到json数组
                .then(reponse=>{
                    this.workerDatas=reponse.data.data            //只取用json数组里的data数据项并把该项数据赋值给workerDatas数组                
                })
            },


    methods:{
        handleSizeChange(val) {
                    this.currentPage = 1;
                    this.pageSize = val;
                    },
            handleCurrentChange(val) {
                        this.currentPage = val;
                    },
            //删除已存在的记录
             deleteList:function(id,index,rows){
                 this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                            }).then(() => {
                             axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'found': id.id,
                                });       
                                axios({
                                        url:this.HOST+'/worker/deleteRecord',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    rows.splice(index, 1);
                                     this.$router.go(0)
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                       }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: '已取消删除'
                            });          
                            });    
                 },                     
    }
}
</script>
<style scoped>
    
</style>


