<template>
    <div class="bgcolor">
        <br>
        <div>
            <p>当前时间：{{date}}</p>
        </div>
        <br><br>
        <div class="title">企业人事管理系统</div>
        <br><br><br>
       <div id="myChart" :style="{width: '400px', height: '300px'}"></div>
        <div id="Address" :style="{width: '400px', height: '300px'}"></div>
        <div class="block">
    <el-carousel height="300px" class='showphoto'>
      <el-carousel-item v-for="item in information">
          <div class="some">
               <h1 class="small">{{ item.message }}</h1>
               <br><br>
                <p class="inform">{{  item.inform }}</p>

          </div>
       
      </el-carousel-item>
    </el-carousel>
  </div>
    </div>
</template>

<script>
import axios from 'axios';
import qs from 'qs';


export default {
  name: 'hello',
  data () {
    return {
      date:new Date(),
      name:[],
      value:[],
      mainData:[],
      information:[
            {'id':1,'message':'通知','inform':'    关于五一期间放假，各部门工作人员分批次交替休息，公司会为大家安排放假期间的休息排班表，如对休息时间有特殊要求，请尽快联系人事处 '},
            {'id':2,'message':'通知','inform':'关于评选优秀员工'},
            {'id':3,'message':'公告','inform':'关于公司招聘内推资格的申请'},
            {'id':4,'message':'公告','inform':'关于本季度公司考勤的统计结果'}
      ]
    }
  },
  created:function(){
            axios.get(this.HOST+'/worker/getMain')            //从后台拿到json数组
                .then(reponse=>{
                     // console.log(reponse)
                       this.mainData = reponse.data.data  
                       let name = []
                       let value = []
                       for(let i=0;i<this.mainData.length;i++){
                         name.push(this.mainData[i].name)
                         value.push(this.mainData[i].value)
                       }
                    //  console.log(name)
                     // console.log(value)


                let myChart = this.$echarts.init(document.getElementById('myChart'))
                // 绘制图表
                        myChart.setOption({
                            title: { text: '企业人员职位展示' },
                            tooltip: {},
                            xAxis: {
                                data: name                        //柱状图横标
                            },
                            yAxis: {},
                            series: [{
                                name: '人数',
                                type: 'bar',
                                data: value                         //柱状图数据
                            }]
                        });
                              
                  let Address = this.$echarts.init(document.getElementById('Address'))
          // 绘制图表
          Address.setOption({
              title: { text: '企业人员位置展示饼图' },
              series : [
                                  {
                              name: '访问来源',
                              type: 'pie',
                              radius: '55%',
                              data: this.mainData                   //饼图数据
                              
                              }
      ]
})
               
             
             
             
             })
             
  },

  mounted(){
 

    let _this = this; // 声明一个变量指向Vue实例this，保证作用域一致
    this.timer = setInterval(() => {
      _this.date = new Date(); // 修改数据date
    }, 1000)

    if(this.item==1){
        
    }

  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
    }
  
},
}
</script>
<style scoped>
 .bgcolor{
     background-color: #e0e8f5;
 }
 #myChart{
     float: left;

 }  
 #Address{
     float: left;

 } 
.bgcolor{
    height: 900px;
}
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
  .showphoto{
      width: 35%
  }
  .title{
    text-align: center;
    font-size: 30px;
    color:darksalmon
  }
  .small{
    font-size: 35px;
    text-align: center;
    color:brown 

  }
  .some{
    height: 100%;
    width: 100%;
  }
  .inform{
    font-size: 20px;
    line-height:40px
  }
</style>


