<template>
    <div>
    <el-menu
      default-active="2"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
        id="leftList"
      >
 
      <el-menu-item index="1" @click="look">
        <i class="el-icon-menu"></i>
        <span slot="title" >员工信息</span>
      </el-menu-item>
           <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>员工管理</span>
        </template>
        <el-menu-item-group class="left">
          <el-menu-item index="1-1"  @click="Record">考勤记录</el-menu-item>
          <el-menu-item index="1-2"  @click="RecordFound">考勤信息</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-menu-item index="3" @click="money">
        <i class="el-icon-document"></i>
        <span slot="title">工作记录</span>
      </el-menu-item>
      
    </el-menu>

    <div class="right">
        <router-view></router-view>
    </div>
    </div>
    
</template>

<script>
export default {
    data(){
        return{
            
        }
    }, mounted:function(){
      var orderHeight=document.body.clientHeight;
      document.getElementById("leftList").style.height=900+'px';
  },
     methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      look:function(){
          this.$router.push('informa')
      },
      Record:function(){
          this.$router.push('record')
      },
      RecordFound:function(){
         this.$router.push('recordFound')
      },
       money:function(){
         this.$router.push('money')
      }
    }
}
</script>
<style scoped>
    
 
.el-menu-vertical-demo{
  text-align: center;
    width:15%;
    float: left;
     position:fixed;
}
.right{
    height:900px ;
    width: 84%;
    float: right;
    background-color: white;

}

</style>


