<template>
    <div class="moneyCss"> 
        <div class="top">
            <el-input v-model="inputYear" type="number" placeholder="" class="inputY" min="2000" max="2030"></el-input>年
            <el-input v-model="inputMouth" type="number" placeholder="" class="inputY" min="1" max="12"></el-input>月
            <el-button type="text" @click="dialogFormVisible = true">配置基本信息</el-button>

                <el-dialog title="当前工资详情" :visible.sync="dialogFormVisible" class="dilog">
                <el-form :model="form">
                     <el-form-item label="设置的年月:" :label-width="formLabelWidth" >
                    <el-input v-model="form.year" type="number" class="input" autocomplete="off"></el-input>年
                     <el-input v-model="form.mouth" type="number" class="input" autocomplete="off"></el-input>月
                    </el-form-item>
                    <el-form-item label="前端人员工资:" :label-width="formLabelWidth" >
                    <el-input v-model="form.FrontEnd" type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="后端人员工资:" :label-width="formLabelWidth">
                    <el-input v-model="form.backEnd" type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="测试人员工资:" :label-width="formLabelWidth">
                    <el-input v-model="form.text" type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="项目经理工资:" :label-width="formLabelWidth">
                    <el-input v-model="form.manage" type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="需求分析工资:" :label-width="formLabelWidth">
                    <el-input v-model="form.need"  type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="加班工资:" :label-width="formLabelWidth">
                    <el-input v-model="form.addWork"  type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="每月假期天数:" :label-width="formLabelWidth">
                    <el-input v-model="form.relax" type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="额外请假扣钱:" :label-width="formLabelWidth">
                    <el-input v-model="form.reduceWork"  type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="迟到罚款:" :label-width="formLabelWidth">
                    前三次<el-input v-model="form.laterleave" type="number" class="inputlate" autocomplete="off"></el-input>
                    超过3次后每次<el-input v-model="form.laterleaveadd" type="number" class="inputlate" autocomplete="off"></el-input>
                    </el-form-item>
                    </el-form-item>
                     <el-form-item label="全勤奖励:" :label-width="formLabelWidth">
                    <el-input v-model="form.allwork" type="number" class="input" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="moneySure">确 定</el-button>
                </div>
                </el-dialog>
        <el-button type="" @click="result">确认查看</el-button>
        </div>

              
            <el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" style="width: 80%"  class="tableCss"  :row-class-name="tableRowClassName">
                <el-table-column     prop="Name"     label="姓名"  >
                </el-table-column>
                <el-table-column   prop="Number" label="职工编号" >
                </el-table-column>
                <el-table-column    prop="Basics"    label="基础工资">
                </el-table-column>
                <el-table-column    prop="Fine"    label="业绩提成">
                </el-table-column>
                <el-table-column    prop="Reward"    label="考勤奖惩">
                </el-table-column>
                <el-table-column    prop="Finaly"    label="最终工资">
                </el-table-column>
            </el-table>
                <div class="block" style="margin-top:15px;">
                        <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10]"  layout="total, sizes, prev, pager, next, jumper" :total="tableData.length">
                        </el-pagination>
                    </div>  

    </div>
</template>

<script>
    import axios from 'axios';
    import qs from 'qs';


export default {
    data(){
        return{
        inputYear:0,
        inputMouth:0,
        dialogFormVisible: false,
        tableData:[],
        currentPage: 1, // 当前页码
                total: 20, // 总条数
                pageSize: 10, // 每页的数据条数
        form: {
        },
        formLabelWidth: '100px'
        };
        
    },
    created:function(){
            axios.get(this.HOST+'/worker/getMoney')            //从后台拿到json数组
                .then(reponse=>{
                    this.form=reponse.data.data[0]            //只取用json数组里的data数据项并把该项数据赋值给workerDatas数组
               })

                let time = new Date();
                 let timeStr =String(time)
                let timeCut = timeStr.split(' ')
                //console.log(timeCut)
                 this.inputYear = parseInt(timeCut[3])
                 let timeMouth = timeCut[1]

                 if(timeMouth=='January'){
                     this.inputMouth= 1
                 }else if(timeMouth=='February'){
                     this.inputMouth= 2
                 }else if(timeMouth=='March'){
                     this.inputMouth= 3
                 }else if(timeMouth=='April'){
                     this.inputMouth= 4
                 }else if(timeMouth=='May'){
                     this.inputMouth= 5
                 }else if(timeMouth=='June'){
                     this.inputMouth= 6
                 }else if(timeMouth=='July'){
                     this.inputMouth= 7
                 }else if(timeMouth=='August'){
                     this.inputMouth= 8
                 }else if(timeMouth=='September'){
                     this.inputMouth= 9
                 }else if(timeMouth=='October'){
                     this.inputMouth= 10
                 }else if(timeMouth=='November'){
                     this.inputMouth= 11
                 }else if(timeMouth=='December'){
                     this.inputMouth= 12
                 }
               
           
    },
    methods:{
             handleSizeChange(val) {
                    this.currentPage = 1;
                    this.pageSize = val;
                    },
            handleCurrentChange(val) {
                        this.currentPage = val;
                    },   


            tableRowClassName({row, rowIndex}) {
               if (rowIndex === 1) {
                    return 'warning-row';
                    } else if (rowIndex === 3) {
                    return 'success-row';
                    }
                    return '';
                },


        moneySure:function(){
            if(this.inputYear<2000||this.inputYear>2030||this.inputMouth<1||this.inputMouth>12){
                    this.$message({
                                        showClose: true,
                                        message: '请输入有效年月信息',
                                        type: 'error'
                                        });
            }else{
              axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                        'year':this.form.year,
                                        'mouth':this.form.mouth,
                                        'FrontEnd':this.form.FrontEnd,
                                        'backEnd':this.form.backEnd,
                                        'text':this.form.text,
                                        'manage':this.form.manage,
                                        'need':this.form.need,
                                        'addWork':this.form.addWork,
                                        'relax':this.form.relax,
                                        'laterleave':this.form.laterleave,
                                        'laterleaveadd':this.form.laterleaveadd,
                                        'lose':this.form.lose,
                                        'allwork':this.form.allwork,
                                        'reduceWork':this.form.reduceWork,
                                });       
                                axios({
                                        url:this.HOST+'/worker/changeMoney',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{

                                    console.log(res)
                                     if(res.data.code==1){
                                        this.$message({
                                        showClose: true,
                                        message: '修改信息成功',
                                        type: 'success'
                                        });
                                        this.dialogFormVisible = false
                                    }else if(res.data.code==3){
                                        this.$message({
                                        showClose: true,
                                        message: '插入新信息成功',
                                        type: 'success'
                                        });
                                         this.dialogFormVisible = false
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '信息更新失败',
                                        type: 'error'
                                        });
                                    }
                                    //console.log(res);
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
            }       
                      },


                        result:function(){

                         if(this.inputYear<2000||this.inputYear>3000||this.inputMouth<1||this.inputMouth>12){
                                        this.$message({
                                        showClose: true,
                                        message: '请输入有效年月信息',
                                        type: 'error'
                                        });
                            }else{   
                             axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'inputYear': this.inputYear,
                                    'inputMouth':this.inputMouth,
                                    
                                });   
                          axios({        
                                    url:this.HOST+'/worker/Finaly',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    console.log(res)
                                     if(res.data.code==2){
                                         this.tableData = res.data.data
                                        
                                }else if(res.data.code==1){
                                        this.$message({
                                        showClose: true,
                                        message: '该月信息不存在，请配置该月基础工资信息',
                                        type: 'error'
                                        });
                                    }
                            
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
    
                    }
                    }

              }
}
</script>
<style scoped>
.input{
    width: 190px;
}
.inputlate{
    width: 100px;
}
.top{
    text-align: right;
}
.dilog{
    text-align: center
}
.inputY{
    width: 90px;
}
 .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
 .tableCss{
     margin-left: 10%
 } 
 .moneyCss{
    height:900px;
    background-color: #e0e8f5;
 }

</style>


