<template>
    <div>
        <br>
        <p>
            <el-dropdown  trigger="click" class="topbutton">
                <el-button type="primary" class="menu">
                    {{message}}<i class="el-icon-arrow-down el-icon--right"></i>
                </el-button>
            
                <el-dropdown-menu slot="dropdown" >
                    <el-dropdown-item ><el-button type="" @click="all">显示所有信息</el-button></el-dropdown-item>
                    <el-dropdown-item ><el-button type="" @click="later">显示迟到信息</el-button></el-dropdown-item>
                    <el-dropdown-item ><el-button type="" @click="rleax">显示请假信息</el-button></el-dropdown-item>
                     <el-dropdown-item ><el-button type="" @click="addwork">显示加班信息</el-button></el-dropdown-item>
                </el-dropdown-menu>
                </el-dropdown-menu>
                </el-dropdown>
            </p>
   
        <div class="massage">
               <router-view></router-view>
        </div>



    </div>
</template>

<script>
export default {
    data(){
        return{
            message:'显示所有信息'
        }
    },
    methods:{
        all:function(){
           this.$router.push('all')
           this.message='显示所有信息'
        },
        later:function(){
            this.$router.push('later')
             this.message='显示迟到信息'
        },
        rleax:function(){
            this.$router.push('rleax')
             this.message='显示请假信息'
        },
        addwork:function(){
             this.$router.push('addwork')
             this.message='显示加班信息'
        }
    }
}
</script>
<style scoped>
.el-dropdown {
    vertical-align: right;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }

p{
    text-align: right
}
.menu{
    width: 170px;
}
.massage{
    height: 686px;
    width: 100%;

}
</style>



