import Vue from 'vue'
import Router from 'vue-router'
import informa from '@/components/page/informa'
import changePassword from '@/components/list/changePassword'
import root from '@/components/common/root'
import workerInform from '@/components/common/workerInforma'
import record from '@/components/page/late'
import recordFound from '@/components/page/lateFound'
import money from '@/components/page/workermoney'
import moneyList from '@/components/page/moneyList'
import main from '@/components/page/main'
import all from '@/components/list/all'
import later from '@/components/list/later'
import rleax from '@/components/list/rleax'
import addwork from '@/components/list/addwork'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/root',
      name: 'root',
      component: root,
      children: [
        {
          path: '/workerInform',
          component: workerInform,
          children: [
            {
              path: '/informa',
              name: 'informa',
              component: informa
                
            },

          


            {
              path: '/money',
              name: 'money',
              component: money
                
            },
            {
              path: '/record',
              name: 'record',
              component: record
                
            },
            {
              path: '/recordFound',
              name: 'recordFound',
              component: recordFound,
                  children: [
                    {
                      path: '/all',
                      name: 'all',
                      component: all
                        
                    },
                    {
                      path: '/later',
                      name: 'later',
                      component: later
                        
                    },{
                      path: '/rleax',
                      name: 'rleax',
                      component: rleax
                        
                    },
                    {
                      path: '/addwork',
                      name: 'addwork',
                      component: addwork
                        
                    },
              ],redirect:'/all',
                
            },
          ],redirect:'/informa'
        },
        
        
        {
          path:'/moneyList',
          name:'moneyList',
          component:moneyList
        },
        {
          path:'/changePassword',
          name:'changePassword',
          component:changePassword
        },

        {
          path:'/main',
          name:'main',
          component:main
        },
      ],redirect:'/main'


    },
    
 
  ]
})




