<template>
    <div class="record">
    <p class="nameBox"> <el-input v-model="nameInput" placeholder="请输入员工姓名" class="input" ></el-input><el-button :plain="true" @click="nameFound">查找</el-button>
           <el-input v-model="numberInput" placeholder="请输入职工号" class="input"></el-input><el-button :plain="true" @click="numberFound">查找</el-button> </p>
        
    <el-table
    :data="workerDatas"  style="width: 100%">
    <el-table-column     prop="Name"    label="姓名"    width="120">
    </el-table-column>
    <el-table-column   prop="Number"   label="职工号"   width="120">
    </el-table-column>
    <el-table-column    fixed="right"    label="操作"  width="800">
      <template slot-scope="scope">
        <el-button @click="dialog=true;handleClick(scope.row)" type="text" size="small">添加项目奖金</el-button>
                    <el-dialog title="添加信息" :visible.sync="dialog" :modal="false">
                            <el-form :model="form">
                                <el-form-item label="流水号:" :label-width="formLabelWidth">
                                    <p>{{Record}}</p>   
                                </el-form-item>
                                <el-form-item label="员工姓名:" :label-width="formLabelWidth">
                                    <p>{{form.Name}}</p>   
                                </el-form-item>
                                <el-form-item label="员工编号:" :label-width="formLabelWidth">
                                <p>{{form.Number}}</p>
                                </el-form-item>
                                <el-form-item label="项目奖金" :label-width="formLabelWidth">
                                <el-input v-model="form.Money" type="number" class="input" autocomplete="off"></el-input>
                                </el-form-item>
                                 <el-form-item label="项目名称" :label-width="formLabelWidth">
                                <el-input v-model="form.things" class="input" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="时间:" :label-width="formLabelWidth">
                                         <el-input v-model="timeYear" class="inputYear" autocomplete="off" type="number" min="2000" max="2030"></el-input>年
                                         <el-input v-model="timeMouth" class="inputtime" autocomplete="off" type="number" min="1" max="12"></el-input>月
                                         <el-input v-model="timeDay" class="inputtime" autocomplete="off" type="number" min="1" max="31"></el-input>日
                                </el-form-item> 
                               
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialog = false">取 消</el-button>
                                <el-button type="primary" @click="changeSure">确 定</el-button>
                            </div>
                            </el-dialog>
      </template>
    </el-table-column>
  </el-table>
<br><br>
<p class="history">历史记录</p>
<br>
  <el-table
    :data="lookDatas.slice((currentPage-1)*pageSize,currentPage*pageSize)"  style="width: 100%">
    <el-table-column     prop="Record"    label="流水号"    width="140">
    </el-table-column>
    <el-table-column     prop="Name"    label="姓名"    width="140">
    </el-table-column>
    <el-table-column   prop="Number"   label="职工号"   width="140">
    </el-table-column>
    <el-table-column   prop="things"   label="项目描述"   width="150">
    </el-table-column>
    <el-table-column   prop="money"   label="项目金额"   width="140">
    </el-table-column>
    <el-table-column   prop="time"   label="考勤时间"   width="140">
    </el-table-column>
    <el-table-column    fixed="right"    label="操作"  width="240">
          <template slot-scope="scope">
                 <el-button @click.native.prevent="deleteList(scope.row,scope.$index,workerDatas)"type="text" size="small">删除</el-button>
                  </template>
    </el-table-column>
  </el-table>
 <div class="block" style="margin-top:15px;">
             <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10]"  layout="total, sizes, prev, pager, next, jumper" :total="workerDatas.length">
               </el-pagination>
           </div>  
 

    </div>
</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
export default {
    data(){
        return{
        dialog: false,
        workerDatas:[],
        timeYear:0,
        timeMouth:0,
        timeDay:0,
        Record:0,
        formLabelWidth: '120px',
        nameInput:'',
        numberInput:'',
        lookDatas:[],
        currentPage: 1, // 当前页码
        total: 20, // 总条数
        pageSize: 10, // 每页的数据条数
        form: {
                    Name: '',
                    Number:'',
                    Money:0,
                    things:'',


              },
        }
    },created:function(){                                     //created 虚拟dom创建时就开始执行的函数  
                         //只取用json数组里的data数据项并把该项数据赋值给workerDatas数组
                //console.log(this.workerDatas)
                 axios.get(this.HOST+'/worker/getMoneyList')            //从后台拿到json数组
                .then(reponse=>{
                  
                   this.lookDatas=reponse.data.data            //只取用json数组里的data数据项并把该项数据赋值给workerDatas数组
                    let List = this.lookDatas
                    let number = []             
                    for(let i=0;i<List.length;i++){
                       number[i]=List[i].Record                         //把data里的数据取出Number项列成一个数组
                   }
                   let newNumber =  parseInt(number[number.length-1])
                   this.Record=newNumber+1  

               })
                let time = new Date();
                 let timeStr =String(time)
                let timeCut = timeStr.split(' ')
                //console.log(timeCut)
                 this.timeYear = parseInt(timeCut[3])
                 this.timeDay = parseInt(timeCut[2])
                 let timeMouth = timeCut[1]

                 if(timeMouth=='January'){
                     this.timeMouth= 1
                 }else if(timeMouth=='February'){
                     this.timeMouth=2
                 }else if(timeMouth=='March'){
                     this.timeMouth=3
                 }else if(timeMouth=='April'){
                     this.timeMouth=4
                 }else if(timeMouth=='May'){
                     this.timeMouth=5
                 }else if(timeMouth=='June'){
                     this.timeMouth=6
                 }else if(timeMouth=='July'){
                     this.timeMouth=7
                 }else if(timeMouth=='August'){
                     this.timeMouth=8
                 }else if(timeMouth=='September'){
                     this.timeMouth=9
                 }else if(timeMouth=='October'){
                     this.timeMouth=10
                 }else if(timeMouth=='November'){
                     this.timeMouth=11
                 }else if(timeMouth=='December'){
                     this.timeMouth=12
                 }
               
            },


    methods:{

         handleSizeChange(val) {
                    this.currentPage = 1;
                    this.pageSize = val;
                    },
            handleCurrentChange(val) {
                        this.currentPage = val;
                    },   
         handleClick(row) {
       // console.log(row);
      },


        nameFound:function(){
                   let nameInputResult=this.nameInput;            //拿到输入内容
                   let reg=/^[\u4e00-\u9fa5]+$/;              //汉字输入的正则表达式
                    if(!reg.test(nameInputResult)){
                          this.$message({
                            showClose: true,
                            message: '输入有误，请输入正确的中文名字',
                            type: 'error'
                        });
                           
                       }else{

                            axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workrName':this.nameInput,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundName',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                   this.nameInput=''
                                    //console.log(res)
                                    if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '未查找到该员工的信息',
                                        type: 'error'
                                     });
                                    }
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                         }
                },


                //按员工号查找
                numberFound:function(){
                let numberInputResult = parseInt(this.numberInput);                    //获取输入值              
                   let rem=/^\d{7}$/;                                         //数字输入的正则表达式
                    if(!rem.test(numberInputResult)){
                      
                           this.$message({
                            showClose: true,
                            message: '未查找到该用户，请确认职工号是否输入正确',
                            type: 'error'
                        });
                        //alert(
                          // alert("未查找到该用户，请确认职工号是否输入正确")
                       }else{
                            

                           axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'workerNumber':numberInputResult,
                                });       
                                axios({
                                        url:this.HOST+'/worker/foundNumber',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    this.numberInput=''
                                   if(res.data.code==1){
                                        this.workerDatas=res.data.data
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '未查找到该员工的信息',
                                        type: 'error'
                                       });
                                    }
                            
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });

                    }

                    },
                    //查询出每一行中的内容，把内容显示到这一行修改界面之中
                handleClick(id) {
                    let foundNumber = id.Number            //拿到传入的参数 
                    let number=this.workerDatas                     
                    let numbers = []       
                    for(let i=0;i<number.length;i++){
                        numbers[i]=number[i].Number              //从所有数据中拿到Number数据
                    }
                    let result = numbers.indexOf(foundNumber)        //匹配传入的参数在整个数据中的位置
                    if(result!=-1){
                        this.form = this.workerDatas[result]          //把这一项内容赋值给form数据，然后修改弹出页面匹配
                    }     
                    },
                //删除已存在的记录
                deleteList:function(id,index,rows){
               // console.log(id.Number)
                 this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                            }).then(() => {
                             axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'found': id.Record,
                                });       
                                axios({
                                        url:this.HOST+'/worker/deleteMoneyList',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                .then((res)=>{
                                    rows.splice(index, 1);
                                     this.$router.go(0)
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                       }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: '已取消删除'
                            });          
                            });    
                 },


                //插入新的奖金信息
                changeSure:function(){
                   //console.log('1')
                    let config = /^[0-9]*$/
                    let text = this.form.Money

                   if(!config.test(text)){
                       this.$message({
                                        showClose: true,
                                        message: '插入失败,只能输入数字',
                                        type: 'error'
                                        });
                   }else{
                        axios.defaults.withCredentials = true 
                                var da1 =  qs.stringify({
                                    'Name': this.form.Name,
                                    'Number':this.form.Number,
                                    'Money':this.form.Money,
                                    'Record':this.Record,
                                    'things':this.form.things,
                                    'timeYear':this.timeYear,
                                     'timeMouth':this.timeMouth,
                                     "timeDay":this.timeDay
                                });       
                                axios({
                                        url:this.HOST+'/worker/addMoney',
                                        method:'post',
                                        data:da1,
                                        headers: {
                                            'Content-Type': 'application/x-www-form-urlencoded'
                                        }
                                        })
                                  .then((res)=>{
                                     if(res.code=1){
                                        this.$message({
                                        showClose: true,
                                        message: '插入奖金成功',
                                        type: 'success'
                                        });
                                    this.$router.go(0)
                                    }else{
                                        this.$message({
                                        showClose: true,
                                        message: '插入奖金失败',
                                        type: 'error'
                                        });
                                    }
                                    //console.log(res);
                                
                                })
                                .catch(function(err) {
                                    console.log(err);
                                });
                     
                            }
                 }

          },
   
}
</script>
<style scoped>
 .input{
    width: 200px;
}   
 .inputtime{
    width: 70px;
}   
 .inputYear{
    width: 80px;
}   
.history{
    text-align: center;
    color: brown;
    font-size: 25px;
}
.nameBox{
    text-align: center;
}
el-dialog{
    text-align: center;
}
</style>


